export default {
    computed: {
        // 
        value() {
            const {
                text,
                mode,
                format,
                href
            } = this
            // 
            if (mode === 'price') {
                // text
                !uni.$u.test.amount(text) && uni.$u.error('text')
                // formatformat
                if (uni.$u.test.func(format)) {
                    // 
                    return format(text)
                }
                // format
                return uni.$u.priceFormat(text, 2)
            } if (mode === 'date') {
                // 
                !uni.$u.test.date(text) && uni.$u.error('text')
                // formatformat
                if (uni.$u.test.func(format)) {
                    // 
                    return format(text)
                } if (this.formart) {
                    // format
                    return uni.$u.timeFormat(text, format)
                }
                // format
                return uni.$u.timeFormat(text, 'yyyy-mm-dd')
            } if (mode === 'phone') {
                // 
                !uni.$u.test.mobile(text) && uni.$u.error('text')
                if (uni.$u.test.func(format)) {
                    // 
                    return format(text)
                } if (format === 'encrypt') {
                    // formatencrypt
                    return `${text.substr(0, 3)}****${text.substr(7)}`
                }
                return text
            } if (mode === 'name') {
                // 
                !(typeof (text) === 'string') && uni.$u.error('text')
                if (uni.$u.test.func(format)) {
                    // 
                    return format(text)
                } if (format === 'encrypt') {
                    // formatencrypt
                    // return text.replace(/(?<=.)./g, '*').substring(0, 3)
                    return text
                }
                return text
            } if (mode === 'link') {
                // 
                !uni.$u.test.url(href) && uni.$u.error('hrefURL')
                return text
            }
            return text
        }
    }
}
